@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
.page {
  display: grid;
  grid-template-areas: "srch ib" "cl cd";
  grid-template-columns: 2fr minmax(400px, 1fr);
  grid-template-rows: minmax(50px, 100px) 1fr;
  margin: 40px 40px 0;
  min-height: calc(100vh - 40px);
  max-width: calc(100vw - 80px); }

@media (max-width: 980px) {
  .page {
    grid-template-areas: "ib" "srch" "cd" "cl";
    grid-template-rows: 70px 70px 500px 1fr;
    grid-template-columns: 1fr; } }

@media (max-width: 430px) {
  .page {
    min-width: 100vw;
    margin: 40px 0 0 10px; } }

.search {
  grid-area: srch; }

.input-form {
  height: 40px;
  width: 350px;
  background: -webkit-gradient(linear, left bottom, left top, from(#92deb5), to(rgba(222, 146, 156, 0)));
  background: linear-gradient(0deg, #92deb5 0%, rgba(222, 146, 156, 0) 100%);
  display: flex;
  flex-direction: row; }

.input-field {
  height: 45px;
  background-color: transparent;
  border-color: transparent;
  flex-grow: 1;
  font-size: 22px;
  color: #243a4e; }

*:focus {
  outline: none; }

.search-button {
  width: 70px;
  height: 40px;
  background: rgba(0, 0, 0, 0.7);
  font-size: 18px;
  font-weight: 300;
  cursor: pointer; }

.icon-bar {
  grid-area: ib;
  display: flex;
  min-height: 45px;
  align-items: flex-start; }

.icons {
  min-height: 45px;
  display: flex;
  align-items: center; }

.navIcon {
  cursor: pointer; }

.errIcon {
  margin-left: 20px; }

.error-text {
  margin-left: 10px;
  color: #D34848;
  font-size: 20px;
  white-space: nowrap; }

.city-list {
  grid-area: cl;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start; }

.city-list {
  grid-area: cl;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start; }

.item {
  height: 220px;
  width: 350px;
  background: rgba(196, 196, 196, 0.06);
  border: 1px solid rgba(196, 196, 196, 0.6);
  border-radius: 6px;
  margin-right: 20px;
  margin-bottom: 20px; }

.item-content {
  margin: 3px 5px 10px;
  height: calc(100% - 13px);
  display: flex;
  flex-direction: column; }

.item-header {
  height: 30px;
  width: 100%;
  display: flex; }

.item-header-city {
  flex-grow: 1;
  font-size: 25px;
  font-weight: 300; }

.item-body {
  display: flex;
  flex-grow: 1; }

.item-body-left {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; }

.item-temperature {
  font-size: 55px;
  color: #C0E7E2; }

.del-icon {
  cursor: pointer;
  width: 30px;
  height: 30px; }

.error-message {
  font-size: 28px; }

.item-body-left img {
  width: 72px;
  height: 72px; }

@keyframes lds-eclipse {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@-webkit-keyframes lds-eclipse {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.lds-eclipse {
  width: 100%;
  height: 100%;
  position: relative; }

.lds-eclipse div {
  position: absolute;
  -webkit-animation: lds-eclipse 1s linear infinite;
  animation: lds-eclipse 1s linear infinite;
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  box-shadow: 0 4px 0 0 #92deb5;
  -webkit-transform-origin: 80px 82px;
  transform-origin: 80px 82px; }

.lds-eclipse {
  width: 200px !important;
  height: 200px !important;
  -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px); }

.lds-css {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.info-block {
  height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.item-info {
  font-size: 24px;
  font-weight: 300;
  height: 40px; }

.current-city {
  grid-area: cd;
  max-height: 650px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 30px; }

.current-city__city-name {
  font-size: 43px; }

.current-city__weather {
  font-size: 26px; }

.current-city__temp {
  display: flex; }

.current-city__temp div {
  display: flex;
  align-items: center; }

.city-default__weather-icon img {
  width: 100px; }

.current-city__temperature {
  font-size: 80px;
  color: #C0E7E2;
  margin-left: 20px; }

* {
  padding: 0;
  margin: 0;
  border: 0;
  font-family: 'Lato', sans-serif;
  color: #FFF4F4; }

html {
  background-image: linear-gradient(221.63deg, #8F9DCC 0%, #58A5A5 69.38%); }

